.tree-panel{
    position: absolute;
    z-index: 100;
    top: 110px;
    left: 100px;
    width: 50rem;
    background: #d7d7d7;
    padding: 1rem;
    height: fit-content;
    border-radius: 0.2rem;
    border: 1px solid #1296db;

  .el-tree{
    width: 200px;
  }
    .leftDiv{
        width:40% ;
        background:#ececec;
        margin-right:1.5rem;
        height:auto;
    }
    .rightDiv{
        width:57%;
        padding:1.1rem;
        background:#ececec
    }
  .plotTreePanel{
    /*position: absolute;*/
    /*!*z-index: 100;*!*/
    /*left: 150px;*/
    width: fit-content;
    height: fit-content;
