  }
  .plotTreePanel{
    & div {
      display: flex;
        overflow-x: hidden;
        flex-wrap: wrap;
    }
  }
  .plotPanelItem{
    display: flex;
    flex-direction: column;
    width: 60px;
    height: 60px;
    background: #ffffff;
    border: 1px solid #1d90e6;
      margin: 0.1rem;
    font-size: 0.7rem;

  }
  .plotTreeSvgItem{
    margin-left: 0.9rem;
    & img {
      width: 1.8rem;
      height: 1.8rem;
    }

  }
}
.site-tree-search-value{
    color: #1d90e6;
}
.titleBg{
    /* background-image: url("../../../assets/images/1.jpg"); */
    background-repeat: no-repeat;
    position: relative;
    top: -2rem;
    background-size: contain;
    span{
        color: #fff;
        font-size: 24px;
        font-weight: bold;
        padding: 1rem;
    }
}
.height{
    display: flex;
    position: relative;
    height: 3rem;
    width: 10rem;
    line-height: 10px;
}
.closeBg{
    /*background-image: url("../../../assets/images/2.jpg");*/
    /*background-repeat: no-repeat;*/
    /*position: relative;*/
    /*top: -2rem;*/

    /*background-size: contain;*/
    a{
        color: #fff;
        font-size: 26px;
        font-weight: bold;
        padding: 0.8rem;

    }

}
.line{
    position: absolute;
    top: -4.1rem;
    left: -1rem;
    height: 3.1rem;
    width: 0.8rem;
    background: #0066cc;
}
/* 直角梯形背景 */
.tree-header h2::after {
    content: '绘制面板';
    position: absolute;
    color: #fff;
    /*display: flex;*/
    /*justify-content: center;*/
    /*align-items: center;*/
    line-height: 3rem;
    padding: 0 1rem;
    left: 0.3rem;
    top: -4rem;
    width: 100%;
    height: 100%;
    background-color: #0066cc;
    clip-path: polygon(
            0 0,
            85% 0,
            100% 100%,
            0 100%
    );
    z-index: -1;
}
/* 关闭背景 */
.tree-close h2::after {
    content: '×';
    position: absolute;
    font-size: 2.3rem;
    display: flex;
    justify-content: end;

    color: #fff;
    line-height: 3rem;
    text-align: center;
    padding: 0 1rem;
    left: 10.8rem;
    top: -4rem;
    width: 25%;
    height: 100%;
    background-color: #0066cc;
    clip-path: polygon(
            0% 0%,
            45% 100%,
            100% 100%,
            100% 0%
    );
    z-index: -1;
}
