html,
body,
#map {
  font-family: "微软雅黑", sans-serif;
  margin: 0;
  height: 100%;
  width: 100%;
  padding: 0;
}

@import url(/node_modules/cesium/Source/Widgets/widgets.css);

#map {
  width: 100vw;
  height: 100vh;
}

#header {
  position: absolute;
  z-index: 100;
  top: 0;
  height: 5rem;
  background-image: url(./images/title_back2.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 25rem;
  left: calc(50% - 12.5rem);

  div {
    font-size: 1.5rem;
    font-weight: 500;
    /* height: 20rem; */
    margin-left: calc(50% - 4.5rem);
    margin-top: 1.2rem;
    /* padding-top: 10rem; */
    color: #ffffff;
    text-shadow: 0 0 0.3rem #1296db;
    /* 第一层发光，绿色，模糊半径10px */
  }
}

#version {
  font-size: 16px;
  margin-top: -28px;
  margin-left: 130px;
  color: #eeeeee;
}

#lyMenu {
  position: absolute;
  top: 60px;
  left: 100px;
  z-index: 100;
}

#txMenu {
  position: absolute;
  top: 90px;
  left: 100px;
  z-index: 100;
}

#toolMenu {
  position: absolute;
  z-index: 100;
  bottom: 2rem;
  left: 1rem;
  width: fit-content;
  padding: 0.5rem;
  border-radius: 2px;
  border: 1px solid #1296db;
  background: #ffffffe6;
}

#menu {
  position: absolute;
  top: 40px;
  left: 20px;
  /* background: rgba(100, 100, 100, 0.85); */
  padding: 10px;
  color: #cccccc;
}

#download {
  float: right;
  font-size: 16px;
  cursor: pointer;
  line-height: 70px;
  margin-right: 20px;
}

#aboutLabel {
  float: right;
  font-size: 16px;
  cursor: pointer;
  line-height: 70px;
}

#aboutContainer {
  position: absolute;
  top: 80px;
  right: 20px;
  width: 450px;
  visibility: hidden;
  color: #eeeeee;
}

#aboutTitle {
  position: absolute;
  top: 0;
  height: 35px;
  line-height: 35px;
  width: 100%;
  background: rgba(100, 100, 100, 255);
  padding-left: 10px;
}

#aboutContent {
  padding-left: 10px;
  position: absolute;
  top: 35px;
  opacity: 0.8;
  height: 230px;
  width: 100%;
  background: rgba(100, 100, 100, 200);
  font-size: 14px;
}

.aboutText {
  padding-left: 5px;
  padding-right: 10px;
  text-indent: 26px;
}

#closeBtn {
  float: right;
  font-size: 16px;
  cursor: pointer;
  padding-right: 10px;
}

button {
  font-family: "微软雅黑", sans-serif;
  opacity: 1;
  min-width: 55px;
}

.attributeUi {
  position: absolute;
  z-index: 2;
  top: 100px;
  right: 100px;
  width: 200px;
  height: auto;
  padding: 20px;
  border-radius: 16px;
  box-sizing: border-box;
  background: #cccccc;
}