.marker-settings-panel {
  background: white;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  width: 360px;
  padding: 16px;
  border: 4px solid rgba(0, 0, 0, 0.1);
  user-select: none;
  margin-top: 40px; /* 为上方的标题栏留出空间 */
}

.panel-header {
  position: absolute;
  top: -7px;
  left: 0;
  font-weight: 600;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: grab;
}

.panel-header h2 {
  position: relative;
  margin: 0;
  color: white;
  z-index: 1;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 8px 36px 8px 0px;
  border-left: 4px solid #0066cc;  /* 添加蓝色竖条 */
  padding-left: 20px;  /* 增加左内边距，让文字与竖条有一定间距 */
}

/* 直角梯形背景 */
.panel-header h2::after {
  content: '';
  position: absolute;
  left: 10px;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #0066cc;
  clip-path: polygon(
    0 0,
    85% 0,
    100% 100%,
    0 100%
  );
  z-index: -1;
}

.close-btn {
  position: absolute;
  /* right: 0; */
  top: 0.3rem;
  left: 10.8rem;
  height: 2.7rem;
  background: none;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  color: #fff;
  /* padding: 4px 8px; */
  background-color: #0066cc;
  clip-path: polygon(
            0% 0%,
            45% 100%,
            100% 100%,
            100% 0%
    );
}
.close-btn::after{
  content: '×';
  font-size: 2rem;
  justify-content: end;
  color: #fff;
  line-height: 1rem;
  text-align: right;
  padding-right: 5px;
  padding-top: -5px;
  display: block;
}

.form-group {
  margin: 12px 0;
  display: flex;
  align-items: center;
}

.form-group label {
  width: 80px;
  text-align: right;
  margin-right: 12px;
  font-size: 13px;
}

.size-input {
  display: flex;
  align-items: center;
  gap: 8px;
}

.size-input input {
  width: 60px;
  text-align: center;
}

.size-input button {
  width: 24px;
  height: 24px;
  border: 1px solid #d9d9d9;
  background: white;
  cursor: pointer;
}


