.style-template-panel {
  margin-bottom: 15px;
  padding-bottom: 10px;
  select{
    /* width: 80%; */
  }
}

.style-template-panel details {
  width: 100%;
}

/* 样式模板标题样式 */
.style-template-panel summary {
  cursor: pointer;
  padding: 5px;
  font-size: 16px;
  border-left: 2px solid #0066cc;
  padding-left: 10px;
  margin-bottom: 10px;
  outline: none;
}

.template-controls {
  padding: 10px;
}

/* 下拉框样式 */
.template-select {
  margin-bottom: 12px;
}

.template-select select {
  width: 100%;
  padding: 6px 8px;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  font-size: 13px;
}

.template-select select:hover {
  border-color: #0066cc;
}

.template-select select:focus {
  border-color: #0066cc;
  outline: none;
  box-shadow: 0 0 0 2px rgba(0, 102, 204, 0.2);
}

/* 按钮组样式 */
.template-actions {
  display: flex;
  gap: 8px;
}

.template-actions button {
  flex: 1;
  padding: 6px 12px;
  border: 1px solid #d9d9d9;
  background: white;
  border-radius: 4px;
  cursor: pointer;
  font-size: 13px;
  transition: all 0.3s;
}

.template-actions button:hover {
  color: #0066cc;
  border-color: #0066cc;
}

.template-actions button:active {
  background: #f0f0f0;
}

/* 特殊按钮样式 */
.template-actions button[title="保存"] {
  background: #0066cc;
  color: white;
  border-color: #0066cc;
}

.template-actions button[title="保存"]:hover {
  background: #005bb8;
  border-color: #005bb8;
}

/* details 展开/收起动画 */
.style-template-panel details[open] summary {
  margin-bottom: 10px;
}

.style-template-panel details[open] .template-controls {
  animation: slideDown 0.3s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
} 
.template-select {
  margin: 8px 0;
}

.template-select-input {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  background-color: #fff;
  color: #606266;
  font-size: 14px;
  transition: border-color 0.2s;
  cursor: pointer;
  outline: none;
}

.template-select-input:hover {
  border-color: #c0c4cc;
}

.template-select-input:focus {
  border-color: #409eff;
}

.template-select-input option {
  padding: 8px 12px;
  background-color: #fff;
}

.template-select-input option:hover {
  background-color: #f5f7fa;
}

/* 模态框样式 */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 4px;
  width: 300px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.15);
}

.modal-content h3 {
  margin: 0 0 16px 0;
  font-size: 16px;
  color: #333;
  border-left: 2px solid #0066cc;
  padding-left: 10px;
}

.template-name-input {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  margin-bottom: 16px;
  font-size: 14px;
  color: #606266;
  transition: border-color 0.2s;
  outline: none;
  box-sizing: border-box;
}

.template-name-input:hover {
  border-color: #c0c4cc;
}

.template-name-input:focus {
  border-color: #0066cc;
  box-shadow: 0 0 0 2px rgba(0, 102, 204, 0.1);
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
}

.modal-btn {
  padding: 6px 16px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s;
  border: 1px solid #dcdfe6;
}

.cancel-btn {
  background: white;
  color: #606266;
}

.cancel-btn:hover {
  color: #0066cc;
  border-color: #0066cc;
}

.confirm-btn {
  background: #0066cc;
  color: white;
  border-color: #0066cc;
}

.confirm-btn:hover {
  background: #005bb8;
  border-color: #005bb8;
}

.confirm-btn:disabled {
  background: #a0cfff;
  border-color: #a0cfff;
  cursor: not-allowed;
}

/* 模态框动画 */
.modal-overlay {
  animation: fadeIn 0.2s ease-out;
}

.modal-content {
  animation: slideUp 0.3s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}